  height: 55px;
  display: flex;
  align-items: center;
  justify-content: end;
  width: -moz-max-content;
  width: max-content;
  padding-left: 45px;
}

.valtas-category-list {
  background: rgba(0, 159, 237, 0.04);
  border-radius: 10px;
  border: 1px solid rgba(0, 0, 0, 0.2);
  padding: 1rem;
}
.valtas-category-list h5, .valtas-category-list .h5 {
  font-size: 26px;
  color: #012F6C;
}
.valtas-category-list .form-check-label {
  color: rgba(27, 27, 27, 0.5);
}

@media (min-width: 992px) {
  .blog-items {
    border-right: 1px solid rgba(0, 0, 0, 0.1);
  }
}
aside.blog-sidebar {
  display: flex;
  flex-flow: column;
}

.cta-sidebar {
  border-radius: 10px;
  padding: 2rem;
}
.cta-sidebar p:last-child {
  margin-bottom: 0;
}
.cta-sidebar a {
  color: #fff;
}

.blog-title {
  font-size: clamp(1.5625rem, 0.3906rem + 3.75vw, 2.5rem);
  color: #012F6C;
}

/****************************************
TEAM BLOCK
*****************************************/
@media (min-width: 992px) {
  .echo-block-team_feed .header-title-left {
    display: flex;
  }
  .echo-block-team_feed .header-title-left .valtas-cta-block__title {
    width: 55%;
  }
  .echo-block-team_feed .header-title-left .valtas-cta-block__intro-text {
    width: 45%;
    padding-left: 2rem;
  }
}
.echo-block-team_feed span.prefix {
  display: block;
  font-size: 20px;
  line-height: normal;
  font-family: "EB Garamond", serif;
  text-transform: uppercase;
}

.card.team-member-card {
  background: #e3e3e3;
  border-radius: 20px;
  overflow: hidden;
  min-height: 443px;
  max-height: 443px;
}
.card.team-member-card:hover {
  background: linear-gradient(180deg, rgb(0, 159, 237) 0%, #012F6C 100%);
}
.card.team-member-card:hover .team-member-info {
  background: linear-gradient(180deg, rgba(1, 47, 107, 0) 60%, #012F6C 70%);
}
.card.team-member-card:hover .team-member-info > * {
  color: white !important;
}
.card.team-member-card:hover .team-member-image::after {
  background: linear-gradient(180deg, rgba(1, 47, 107, 0.5) 60%, #012f6c 100%);
}
.card.team-member-card .btn-valtas::before {
  background: url(../img/btn-icon.png);
}
.card.team-member-card .btn-valtas a {
  height: 55px;
  background: white;
  border-color: #012F6C;
  color: #012F6C !important;
  font-weight: 600;
}
.card.team-member-card .btn-valtas a::before {
  background-size: cover;
}
.card.team-member-card .btn-valtas a:hover {
  background: linear-gradient(180deg, rgb(1, 47, 107) 0%, rgb(0, 159, 237) 100%);
  color: white !important;
}
.card.team-member-card .team-member-info {
  padding: 1rem;
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  flex-flow: column;
  justify-content: end;
  background: linear-gradient(180deg, rgba(228, 228, 228, 0) 20%, rgb(228, 228, 228) 100%);
}
.card.team-member-card .team-member-image {
  position: relative;
}
.card.team-member-card .team-member-image::after {
  content: "";
  background: linear-gradient(180deg, rgba(228, 228, 228, 0) 60%, rgb(228, 228, 228) 100%);
  width: 100%;
  display: block;
  height: 100%;
  top: 0;
  position: absolute;
  left: 0;
}
.card.team-member-card a.socials {
  background: white;
  width: 35px;
  height: 35px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 100%;
}
.card.team-member-card .card-title {
  color: #012F6C;
  font-size: 27px;
}

.team-profile-popup .modal-content {
  border-radius: 30px;
  background-image: url("../img/profile-bg.jpg");
  padding: 2rem;
  background-size: cover;
  background-position: top center;
}
.team-profile-popup .tp-image {
  background: #F1F1F1;
  border: 4px solid white;
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.3);
  aspect-ratio: 1;
}
.team-profile-popup .tp-position {
  font-size: clamp(1.25rem, 1.118rem + 0.4225vw, 1.625rem);
  text-transform: uppercase;
  margin-bottom: 0;
  color: #fff;
}
.team-profile-popup .tp-name {
  color: #fff;
  margin-bottom: 2rem;
}
.team-profile-popup .socials {
  min-width: 53px;
  height: 53px;
  display: flex;
  background: white;
  border-radius: 100%;
  align-items: center;
  justify-content: center;
}
.team-profile-popup button.btn-close {
  position: absolute;
  right: -10px;
  top: -10px;
  background-color: white;
  border: 3px solid #009FED;
  opacity: 1;
  border-radius: 100%;
  padding: 1rem;
}
.team-profile-popup .tp-image img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  object-position: top;
}

/****************************************
REVIEW CTA BLOCK
*****************************************/
#reviewCarousel {
  background-image: url("../img/testi-bg.png");
  min-height: 420px;
  width: 100%;
  max-width: 671px;
  background-size: contain;
  background-repeat: no-repeat;
  display: flex;
  align-items: center;
  padding: 2rem;
  position: relative;
  margin-left: auto;
}
@media (max-width: 991.98px) {
  #reviewCarousel {
    background: linear-gradient(0deg, rgb(34, 118, 162) 0%, rgb(24, 54, 86) 100%);
  }
}
#reviewCarousel .testimonial-title {
  max-width: 80%;
}
#reviewCarousel .carousel-controls {
  display: flex;
  position: absolute;
  bottom: -15px;
  right: 5%;
  gap: 10px;
}
#reviewCarousel .carousel-controls button {
  background: white;
  width: 40px;
  height: 40px;
  border-radius: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 1;
  position: relative;
}
#reviewCarousel .carousel-controls button span {
  filter: brightness(0) saturate(100%) invert(12%) sepia(40%) saturate(4860%) hue-rotate(206deg) brightness(96%) contrast(99%);
}

